<template>
  <AllProjects v-if=showAllProjects />
  <Project v-else-if=showProject :project=project />
</template>

<script>

import { eventBus } from "../main";

export default {
  name: "MainContent", 

  components: {
    AllProjects : () => import("./AllProjects.vue"), 
    Project : () => import("./Project.vue"),
    // About : () => import("./About"), 
    // Contact : () => import("./Contact"), 
  }, 

  data() {
    return {
      showAllProjects : true, 
      showProject : false, 
      showAbout : false, 
      showContact : false, 
      project : {}
    }
  }, 

  created: function(){
    eventBus.$on(("show-all-projects"), () => {
      this.setViewsFalse(); 
      this.showAllProjects = true; 
    }); 

    eventBus.$on(("show-one-project"), (project) => {
      this.setViewsFalse(); 
      this.showProject = true; 
      this.project = project; 
    }); 

    eventBus.$on(("show-about"), () => {
      this.setViewsFalse(); 
      this.showAbout = true; 
    });

    eventBus.$on(("show-contact"), () => {
      this.setViewsFalse(); 
      this.showContact = true; 
    });
  }, 

  methods : {
    setViewsFalse() {
      this.showAllProjects = false; 
      this.showProject = false; 
      this.showAbout = false; 
      this.showContact = false; 
    }
  }
}
</script>